section.why + section.start {
  .gt(@break-point-pad, {
    display: none;
  });
}

section.start {
  background-color: @bg-accent;
  background-image: url(./bg-start.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  padding-top: 56px;
  padding-bottom: 64px;

  .gt(@break-point-pad, {
    background-image: url(./bg-start-desktop.svg);
    padding-top: 120px;
    padding-bottom: 120px;
  });

  .wrap {
    max-width: 550px;
  }

  h2 {
    margin-bottom: 20px;

    .gt(@break-point-pad, {
      margin-bottom: 24px;
    });

    br {
      .gt(@break-point-pad, {
        display: none;
      });
    }
  }

  p {
    text-align: center;
    font-size: 14px;
    line-height: 1.4;

    margin-bottom: 8px;

    &:last-child {
      margin-bottom: 40px;
    }

    .gt(@break-point-pad, {
      font-size: 16px;
      line-height: 24px;
    });
  }
}
