section.money {
  @break-point: @break-point-desktop;

  padding-top: 85px;
  padding-bottom: 80px;
  text-align: center;

  background-image: url(./exclamation.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: auto 78%;

  .gt(@break-point, {
    background: unset;
  });

  .numbers {
    .gt(@break-point, {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
    });

    .number {
      margin-bottom: 48px;

      .gt(@break-point, {
        width: 30%;
        margin-bottom: 60px;
      });

      &:last-child {
        margin-bottom: 0;
      }

      .description {
        font-size: 16px;
        line-height: 140%;

        margin-bottom: 8px;

        .gt(@break-point-pad, {
          font-size: 22px;
          line-height: 28px;
          max-width: 80%;
          margin: 0 auto;
          margin-bottom: 18px;
        });

        span {
          display: none;
          .gt(@break-point, {
            display: inline;
          });
        }
      }

      .value {
        font-size: 48px;
        line-height: 100%;

        font-family: @font-title;
        color: @main-accent;

        .gt(@break-point-pad, {
          font-size: 54px;
          line-height: 120%;
          text-transform: uppercase;
        });

        span {
          display: inline;
          .gt(@break-point, {
            display: none;
          });
        }
      }
    }
  }

  img.bricks {
    display: none;

    .gt(@break-point, {
      display: block;
      width: 100%;
    });
  }
}
