header {
  @break-point: @break-point-desktop;

  position: sticky;
  top: 0;

  padding: @m-padding;
  padding-bottom: 0;
  background-color: @bg-color;

  .gt(@break-point, {
    padding: @d-padding-h @d-padding-v 0;
  });

  nav {
    border: @border;
    height: 40px;

    .gt(@break-point, {
      height: 56px;
    });

    ul {
      display: flex;
      flex-direction: row;
      height: 100%;

      li {
        flex: 0 0 46px;

        display: flex;
        align-items: center;
        justify-content: center;

        border-right: @border;

        .gt(@break-point, {
          flex: 0 0 56px;
          outline-color: @main-accent;
        });

        &:last-child {
          border-right: 0;
        }

        &.play-market,
        &.app-store {
          img {
            height: 20px;
            width: 20px;

            .gt(@break-point, {
              height: 24px;
              width: 24px;
            });
          }
        }

        &.logo {
          flex-grow: 2;

          a {
            .gt(@break-point, {
              max-width: 92px;
              outline: 2px solid @border-color;
              outline-offset: 0px;
              margin-right: -16px; /* see .login */
            });

            &:focus {
              outline-offset: 0px;
              outline-width: 2px;
            }

            img {
              .gt(@break-point, {
                height: 30px;
              });
            }
          }
        }

        &.login {
          span {
            .gt(@break-point, {
              padding: 16px;
            });
          }
        }

        a {
          border-left: @border-width solid transparent;
          font-family: @font-title;
          color: @main-color;
          font-weight: 500;
          font-size: 12px;
          text-transform: uppercase;
          text-decoration: none;

          width: 100%;
          height: 100%;

          display: flex;
          align-items: center;
          justify-content: center;

          .gt(@break-point, {
            font-size: 14px;
            line-height: 22px;
          });

          &:focus {
            .gt(@break-point, {
              outline-width: 2px;
              outline-color: @main-accent;
            });
          }
        }
      }
    }
  }
}
