section.friends {
  @break-point-friends: 600px;

  h2 {
    margin-bottom: 24px;

    .gt(@break-point-pad, {
      font-size: 32px;
      line-height: 42px;
    });
  }

  .tiles {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 16px;

    .gt(@break-point-friends, {
      grid-template-columns: repeat(4, 1fr);
    });

    .tile {
      height: 60px;

      background-color: #FFFFFF;
      display: flex;
      align-items: center;
      justify-content: center;

      .gt(@break-point-desktop, {
        height: 120px;;
      });

      img {
        .gt(@break-point-desktop, {
          width: 160px;
        });

        &.kontur {
          max-width: 120px;
        }
      }
    }
  }
}
