section.how {
  .gt(@break-point-desktop, {
    padding-top: 80px;
    padding-bottom: 80px;
  });

  h2 {
    border: @border;
    padding: 8px;
    .gt(@break-point-desktop, {
      padding: 40px 95px;
      width: 740px;
      margin: 0 auto;
    });

    br {
      display: none;
      .gt(@break-point-desktop, {
        display: inline;
      });
    }
  }

  ul {
    counter-reset: how-counter;

    /* fake borders */

    &:before,
    &:after {
      content: "";
      display: block;
      width: 50%;
      height: 32px;
      border-right: @border;

      .gt(@break-point-pad, {
        height: 60px;
      });
    }
    &:before {
      border-bottom: @border;
    }
    &:after {
      border-top: @border;
    }

    li {
      padding: 12px;

      font-family: @font-title;
      font-size: 16px;
      line-height: 1.25;
      text-transform: uppercase;
      display: flex;
      align-items: center;

      .gt(@break-point-desktop, {
        padding: 40px;

        font-size: 32px;
        line-height: 42px;
      });

      br {
        .gt(@break-point-pad, {
          display: none;
        });
      }

      /* numbers */
      &:before {
        font-family: @font-title;
        color: @main-accent;
        font-size: 44px;
        line-height: 36px;

        display: block;
        counter-increment: how-counter;
        content: "0" counter(how-counter);
        min-width: 30px;
        margin-top: -2px;

        .gt(@break-point-desktop, {
          font-size: 70px;
          min-width: 80px;
          margin-top: -8px;
        });
      }

      &:nth-child(odd) {
        border-left: @border;
        border-bottom: @border;

        &:before {
          margin-right: 12px;

          .gt(@break-point-desktop, {
            margin-right: 40px;
          });
        }
      }
      &:nth-child(even) {
        border-right: @border;
        border-bottom: @border;

        text-align: right;
        flex-direction: row-reverse;

        &:before {
          margin-left: 12px;

          .gt(@break-point-desktop, {
            margin-left: 40px;
          });
        }
      }
      &:last-child {
        border-bottom: 0;
      }

    }
  }

  .button {
    .gt(@break-point-pad, {
      width: 250px;
      border-width: 3px;
    });
  }
}
