/* */

html, body {
  background-color: @bg-color;
  color: @main-color;
  font-family: @font-text;
  scroll-behavior: smooth;
}

main > section {
  padding: @m-padding;
  scroll-margin: 60px;

  .gt(@break-point-desktop, {
    padding: @d-padding-h @d-padding-v;
    box-sizing: content-box;
  });
}

.wrap {
  max-width: 960px;
  margin: 0 auto;
}

/* headers */

h1, h2, h3, h4, h5, h6 {
  font-family: @font-title;
  font-style: normal;
  font-weight: normal;

  text-align: center;
  text-transform: uppercase;
}

h1 {
  font-size: 34px;
  line-height: 44px;

  .gt(@break-point-pad, {
    font-size: 54px;
    line-height: 1.2;
  });
}

h2 {
  font-size: 32px;
  line-height: 40px;

  .gt(@break-point-pad, {
    font-size: 54px;
    line-height: 120%;
  });
}

/* etc */

* {
  box-sizing: border-box;
}

*::selection {
  color: #000000;
  background: @main-accent;
}

*:focus {
  outline: 1px solid currentColor;
  outline-offset: 0px;
}

.button {
  display: block;
  width: 100%;
  box-sizing: border-box;

  border: @border-width solid @main-color;
  padding: @m-padding;

  color: @main-color;
  font-family: @font-title;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  max-width: 350px;
  margin: 0 auto;

  text-decoration: none;
  text-align: center;
  text-transform: uppercase;

  cursor: pointer;
  background-color: unset;

  transition:
    color 0.1s ease-in,
    background-color 0.1s ease-in;

  .gt(@break-point-pad, {
    font-size: 20px;
    line-height: 30px;
  });

  &:hover {
    background-color: @main-color;
    color: #000;

    img {
      filter: invert(1);
    }
  }

  img {
    transition: filter 0.1s ease-in;
  }

  &.accent {
    border-color: @main-accent;

    &:hover {
      background-color: @main-accent;
    }

    &:focus {
      outline-color: @main-accent;
    }
  }
}

header, footer {
  a {
    transition:
    color 0.1s ease-in,
    background-color 0.1s ease-in;

    img {
      transition: filter 0.1s ease-in;
    }

    &:hover {
      background-color: @main-color;
      // outline: @border-width solid @main-color;
      color: #000000;

      img {
        filter: invert(1);
      }
    }
  }
}
