footer {
  padding: @m-padding;
  padding-top: 32px;

  font-size: 12px;
  line-height: 16px;

  .gt(@break-point-desktop, {
    font-size: 14px;
    line-height: 20px;
    padding: 60px 40px;
  });

  nav {
    border-bottom: @border;
    border-right: @border;

    ul {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;

      .gt(@break-point-desktop, {
        flex-wrap: unset;
      });

      .spacer {
        display: none;
        .gt(@break-point-desktop, {
          display: block;
          margin-right: auto;
          flex-grow: 1;
        });
      }

      li {
        flex: 1 1 100%;

        border: @border;
        border-bottom: 0;
        border-right: 0;

        .gt(@break-point-desktop, {
          flex: unset;
        });

        span, a {
          padding: 11px;
          display: block;
          color: inherit;
          text-decoration: none;
          position: relative;

          .gt(@break-point-desktop, {
            padding: 10px 16px;
          });

          &:focus {
            outline: @border-width solid @main-color;
          }
        }
      }
    }
  }


  .top {
    .mail, .feedback {
      flex-basis: 50%;

      .gt(@break-point-desktop, {
        flex-basis: auto;
      });
    }

    .appstore, .googleplay {
      img {
        width: 16px;
        height: 16px;
        margin-top: -2px;
        margin-bottom: -3px;
        margin-right: 6px;
      }
    }
  }

  .bottom {
    .rules {
      a {
        color: fadeout(@main-color, 40%);

        .gt(@break-point-desktop, {
          color: inherit;
        });

        &:hover {
          color: #000000;
        }
      }
    }
    .social {
      min-width: 40px;
      flex-basis: 25%;
      display: flex;
      align-items: center;
      justify-content: center;

      .gt(@break-point-desktop, {
        flex-basis: auto;
      });


      a {
        height: 100%;
        width: 100%;
        padding: 9px;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
}
