section.apps {
  @break-point-apps: 500px;

  padding-top: 36px;
  margin-bottom: -30px;
  overflow: hidden;

  .gt(@break-point-pad, {
    padding-top: 40px;
    padding-bottom: 60px;
    margin-bottom: 0px;
  });

  .wrap {
      padding-right: 0;
      display: flex;
      flex-direction: row;

    .gt(@break-point-pad, {
      padding-left: 30px;
      padding-right: 30px;
    });

    & > * {
      flex: 1 1 auto;
    }
  }

  &:after {
    content: "";
    display: block;
    clear: both;
  }

  h2 {
    text-align: left;
    margin-top: 14px;
    margin-bottom: 16px;

    .gt(@break-point-pad, {
      margin-bottom: 30px;
      margin-top: 44px;
    });} h2 {
    .gt(@break-point-desktop, {
    });
  }

  p {
    font-size: 14px;
    line-height: 1.4;
    margin-bottom: 8px;

    &:last-child {
      margin-bottom: 20px;

      .gt(@break-point-pad, {
        margin-bottom: 40px;
      });
    }

    .gt(@break-point-pad, {
      font-size: 16px;
      line-height: 24px;
    });} p {
    .gt(@break-point-pad, {
      max-width: 374px;
    });
  }

  .buttons {
    .gt(@break-point-pad, {
      display: flex;
      flex-direction: row;
    });
  }

  .button {
    font-family: @font-text;
    font-weight: normal;
    font-size: 16px;
    line-height: 16px;

    text-align: left;
    text-transform: none;

    width: 170px;
    padding: 8px 12px 12px;

    display: flex;
    align-items: center;
    flex-direction: row;

    margin: 0;
    margin-bottom: 24px;

    .gt(@break-point-pad, {
      margin-right: 24px;
    });

    img {
      pointer-events: none;
      margin-right: 12px;
      margin-top: 2px;
    }

    small {
      display: block;
      font-size: 12px;
      line-height: 16px;
      margin-bottom: 2px;
    }
  }

  .info {
    max-width: 188px;
    flex-shrink: 0;

    .gt(@break-point-apps, {
      max-width: 50%;
    });
  } .info {
    .gt(@break-point-pad, {
      min-width: 385px;
    })
  }

  .phones {
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-start;
    padding-right: 16px;

    .phone-1 {
      margin-right: -32px;
      display: block;

      .gt(@break-point-pad, {
        margin-right: 0;
        display: block;
      });
    }

    .phone-2 {
      display: none;

      .gt(@break-point-pad, {
        display: block;
        margin-left: -100px;
        margin-top: 35px;
      });
    }
  }
}
