@import url(~/node_modules/reset.css/reset.css);

@import url(~/node_modules/@fontsource/oswald/index.css);
@import url(~/node_modules/@fontsource/oswald/500.css);

@import url(~/node_modules/@abdt/fonts/style.css);

/* fonts */

@font-title: "Oswald", "Arial Narrow", "Helvetica Condensed", monospace;
@font-text: "Golos", sans-serif;

/* colors */

@main-color: #FFFFFF;
@main-accent: #FFD140;

@bg-color: #82A8BD;
@bg-accent: #3875A8;

/* borders */

@border-color: #9FBDCE;
@border-width: 2px;
@border: @border-width solid @border-color;

/* paddings */

@m-padding: 16px;
@d-padding-h: 24px;
@d-padding-v: 40px;

/* mixins */

@break-point-pad: 700px;
@break-point-desktop: 1024px;

.gt(@min, @styles) {
  @media (min-width: @min) {
    @styles();
  }
}

/* common */

@import "./sections/common/common.less";

/* sections */

@import "./sections/header/header.less";

@import "./sections/hero/hero.less";
@import "./sections/why/why.less";
@import "./sections/start/start.less";
@import "./sections/money/money.less";
@import "./sections/how/how.less";
@import "./sections/apps/apps.less";
@import "./sections/friends/friends.less";
@import "./sections/poster/poster.less";

@import "./sections/footer/footer.less";
