section.why {
  padding-top: 48px;
  padding-bottom: 64px;

  .gt(@break-point-desktop, {
    padding-top: 80px;
    padding-bottom: 75px;
  });

  h2 {
    margin-bottom: 24px;

    .gt(@break-point-desktop, {
      border: @border;
      border-bottom: 0;
      padding: 25px;
      margin-bottom: 0;
    });
  }

  ol {
    counter-reset: why-counter;

    li {
      border: @border;
      border-bottom: 0;
      padding: @m-padding;

      &:last-child {
        border-bottom: @border;
      }

      &:before {
        font-family: @font-title;
        font-weight: 300;
        font-size: 60px;
        line-height: 91%;

        display: block;
        counter-increment: why-counter;
        content: counter(why-counter);
        float: left;
        min-width: 30px;
        margin-right: 12px;
      }

      h3 {
        font-size: 22px;
        line-height: 30px;
        text-align: left;
        margin-bottom: 12px;
      }

      p {
        font-size: 14px;
        line-height: 140%;
        margin-bottom: 8px;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }

  }
}

section.why {
  .gt(@break-point-pad, {
    padding-bottom: 24px;
  });
}
