section.hero {
  @break-point: @break-point-pad;

  padding-top: 40px;

  .gt(@break-point, {
    padding-top: 80px;
    padding-bottom: 80px;
  });

  .lead {
    .gt(@break-point, {
      display: flex;
      flex-direction: row;
    });

    .brick {
      display: none;

      .gt(@break-point-desktop, {
        display: block;
      });
    }

    .content {
      .gt(@break-point, {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0 36px;
      });

      h1 {
        margin-bottom: 24px;

        .gt(@break-point, {
          margin-top: -10px;;
          margin-bottom: 30px;
          max-width: 462px;
        });
      }

      p.subtitle {
        font-size: 16px;
        line-height: 1.5;

        text-align: center;

        margin-bottom: 28px;

        .gt(@break-point, {
          font-size: 20px;
          line-height: 150%;
        });
      }

      .actions {
        margin-bottom: 32px;

        .gt(@break-point, {
          display: flex;
          justify-content: center;
          align-items: baseline;
          width: 100%;
          max-width: 500px;
          margin-top: auto;
          margin-bottom: @border-width;
        });

        .button.accent {
          margin-bottom: 16px;

          .gt(@break-point, {
            margin-right: 24px;
            margin-bottom: 0;
          })
        }
      }
    }
  }

  .arrow {
    display: none;

    .gt(@break-point, {
      display: block;
      margin: 0 auto;
      margin-top: 90px;
      margin-bottom: 42px;
    })
  }

  .demo {
    scroll-margin: 100px;
    border: @border;
    background-color: @main-color;
    width: 100%;

    .gt(@break-point, {
      border-width: 9px;
    })
  }
}
